.skills-card
    background-color: #DCD6A4
    display: flex
    flex-direction: column
    justify-content: space-between

.skills-card-container
    margin-left: 48px
    margin-right: 12px

.skills-card-title
    border-bottom: 5px solid #324C5F

.skills-card-title h1
    position: relative
    margin-bottom: 12px
    left: -42px

.skills-card-list
    h3
        margin-top: 12px
    ul
        display: flex
        flex-direction: column
        flex-wrap: wrap
    ul li
        color: #324C5F

.skills-card
    .card-navigation 
        button
            color:#324C5F
        button svg path
            fill: #324C5F

.slided-card
    margin-top: 12px
    position: relative
    left:-72px
    background-color: #324C5F
    padding: 6px 0 0 6px
    h3
        color: #FBFBFB
        margin: 0
    ul li
        color: #FBFBFB
        margin-top: 6px
