.menu
    position: fixed
    z-index: 1001
    top: 0px
    left: 0px
    display: flex
    flex-direction: column
    border-right: 2px solid transparent
    

.menu-content
    display: none
    height: 100%
    flex-direction: column
    justify-content: space-between
    
    
.menu-opened
    background-color: rgb(30 156 161 / 95%)
    height: 100%
    min-width: 30%
    .menu-content
        display: flex
        opacity: 1

@media (max-width: 768px)
    .menu-opened
        min-width: 50%

@media (max-width: 425px)
    .menu-opened
        min-width: 100%

.menu-button-container button
    border: none
    background: none
    cursor: pointer
    height: 32px
    width: 32px
    overflow: hidden

.menu-button-container
    padding: 30px
    height: 32px
    width: 32px
    transition: transform 1.2s

.menu-container
    height: 100%
    .menu-options
        span
            font-size: 32px
            font-weight: bold
            padding: 10px 30px
            cursor: pointer
            transition: color 0.5s,background 1s
        span:hover
            color: white
            background: #ffffff47
    
.menu-options
    flex-direction: column
    display: flex

.menu-footer 
    span
        height: 55px
        display: block
        font-size: 32px
        font-weight: bold
        text-decoration: underline
        text-align: center
        cursor: pointer
        transition: color 0.5s
    span:hover
        color: white

