.portfolioboard
    overflow: hidden

.localization-button-container
    position: absolute
    bottom: 20px
    left: 20px
    z-index: 100

.localization-button
    border: 0
    background: none
    text-decoration: underline
    transition: opacity 0.5s
    cursor: pointer
    font-size: 24px
    opacity: 0.5

.localization-button:hover
    opacity: 1

.portfolioboard-object
    z-index: 2
    position: absolute
    cursor: initial
    // overflow: hidden
    opacity: 0.9
    transition: opacity 1.2s

.portfolioboard-object:hover
    opacity: 1

.portfolioboard-map
    position: fixed
    opacity: 0.7
    transition: opacity 0.5s
    background-color: $mapColor
    border-radius: 6px
    padding-right: 2px
    padding-bottom: 2px
    z-index: 150
    box-shadow: 0 0 5px 1px $mapColor

@media (max-width: 425px)
    .portfolioboard-map
        display: none

.portfolioboard-map:hover
    opacity: 1

.portfolioboard-map-window
    z-index: 3
    cursor: pointer
    position: absolute
    background-color: $mapWindow
    // border: 1px $mapWindow solid
    border-radius: 6px
    // box-shadow: 0 0 3px 1px
    transition: transform 0.2s
    overflow: hidden

.portfolioboard-map-object
    position: absolute
